import {
  Component, OnInit, Input, Output, ViewChild, ElementRef,
  EventEmitter, Renderer2, forwardRef
} from '@angular/core';
import {NG_VALUE_ACCESSOR, ControlValueAccessor} from '@angular/forms';

import {CommandExecutorService} from './common/services/command-executor.service';
import {MessageService} from './common/services/message.service';

import {ngxEditorConfig} from './common/ngx-editor.defaults';
import * as Utils from './common/utils/ngx-editor.utils';

@Component({
  selector: 'app-ngx-editor',
  templateUrl: './ngx-editor.component.html',
  styleUrls: ['./ngx-editor.component.scss'],
  providers: [{
    provide: NG_VALUE_ACCESSOR,
    useExisting: forwardRef(() => NgxEditorComponent),
    multi: true
  }]
})

export class NgxEditorComponent implements OnInit, ControlValueAccessor {
  @Input() editable: boolean;
  @Input() spellcheck: boolean;
  @Input() placeholder: string;
  @Input() translate: string;
  @Input() height: string;
  @Input() minHeight: string;
  @Input() width: string;
  @Input() minWidth: string;
  @Input() toolbar: Object;
  @Input() resizer = 'stack';
  @Input() config = ngxEditorConfig;
  @Input() showToolbar: boolean;
  @Input() enableToolbar: boolean;
  @Input() imageEndPoint: string;

  @Output() blur: EventEmitter<string> = new EventEmitter<string>();
  @Output() focus: EventEmitter<string> = new EventEmitter<string>();

  @ViewChild('textArea', {static: false}) textArea: any;
  @ViewChild('ngxWrapper', {static: false}) ngxWrapper: ElementRef;

  Utils: any = Utils;

  private onChange: (value: string) => void;
  private onTouched: () => void;

  constructor(private _messageService: MessageService, private _commandExecutor: CommandExecutorService, private _renderer: Renderer2) {
  }

  onTextAreaFocus(): void {
    this.focus.emit('focus');
  }

  onEditorFocus() {
    let textArea: any = document.getElementById('textArea');
    ((this.textArea || {}).nativeElement || textArea).focus();
  }

  onContentChange(innerHTML: string): void {
    if (typeof this.onChange === 'function') {
      this.onChange(innerHTML);
      this.togglePlaceholder(innerHTML);
    }
  }

  onTextAreaBlur(): void {
    /** save selection if focussed out */
    this._commandExecutor.savedSelection = Utils.saveSelection();

    if (typeof this.onTouched === 'function') {
      this.onTouched();
    }
    this.blur.emit('blur');
  }

  resizeTextArea(offsetY: number): void {
    let newHeight = parseInt(this.height, 10);
    newHeight += offsetY;
    this.height = newHeight + 'px';
    let textArea: any = document.getElementById('textArea');
    ((this.textArea || {}).nativeElement || textArea).style.height = this.height;
  }

  executeCommand(commandName: string): void {
    try {
      this._commandExecutor.execute(commandName);
    } catch (error) {
      this._messageService.sendMessage(error.message);
    }
  }

  writeValue(value: any): void {
    this.togglePlaceholder(value);

    if (value === null || value === undefined || value === '' || value === '<br>') {
      value = null;
    }

    this.refreshView(value);
  }

  registerOnChange(fn: any): void {
    this.onChange = fn;
  }

  registerOnTouched(fn: any): void {
    this.onTouched = fn;
  }

  refreshView(value: string): void {
    let textArea: any = document.getElementById('textArea');
    const normalizedValue = value === null ? '' : value;
    this._renderer.setProperty((this.textArea || {}).nativeElement || textArea, 'innerHTML', normalizedValue);
  }

  togglePlaceholder(value: any): void {
    console.log(this.ngxWrapper);
    let ngxWrapper: any = document.getElementById('ngxWrapper');
    if (!value || value === '<br>' || value === '') {
      this._renderer.addClass((this.ngxWrapper || {}).nativeElement || ngxWrapper, 'show-placeholder');
    } else {
      this._renderer.removeClass((this.ngxWrapper || {}).nativeElement || ngxWrapper, 'show-placeholder');
    }
  }

  getCollectiveParams(): any {
    return {
      editable: this.editable,
      spellcheck: this.spellcheck,
      placeholder: this.placeholder,
      translate: this.translate,
      height: this.height,
      minHeight: this.minHeight,
      width: this.width,
      minWidth: this.minWidth,
      enableToolbar: this.enableToolbar,
      showToolbar: this.showToolbar,
      imageEndPoint: this.imageEndPoint,
      toolbar: this.toolbar
    };
  }

  ngOnInit() {
    this.config = this.Utils.getEditorConfiguration(this.config, ngxEditorConfig, this.getCollectiveParams());
    let textArea: any = document.getElementById('textArea');
    this.height = this.height || ((this.textArea || {}).nativeElement || textArea).offsetHeight;

    this.executeCommand('enableObjectResizing');
  }
}
